export default {
	data() {
		return {
			// 控制确定按钮是否显示绿色
			isDetermine: false,
			// 控制取消按钮是否显示绿色
			isCancel: false,
			// 控制第一个弹框内容的显示与隐藏
			isBox: true,
			// 控制第二个弹框内容的显示与隐藏
			isReplace: false
		}
	},
	methods: {
		// 点击退款按钮 打开弹框
		refund() {
			this.$refs.popup.open()
		},
		// 点击了取消按钮
		cancel() {
			this.isCancel = !this.isCancel
			// 关闭弹框
			this.$refs.popup.close()
		},
		// 点击了确定按钮
		determine() {
			this.isDetermine = !this.isDetermine
			this.isBox = !this.isBox
			this.isReplace = !this.isReplace
		},
		// 点击知道了 关闭弹框
		closeKnow() {
			this.$refs.popup.close()
		}
	}
}
